.form-wrapper {
  background-color: transparent;
  max-width: 550px;
  margin: 0px auto;
  color: #262626;
  padding: 30px 0; }

.form-wrapper * {
  outline: none; }

.form__inner {
  display: flex;
  justify-content: space-between; }

.form__body {
  position: relative; }

.form__body::before {
  content: '';
  position: absolute;
  bottom: -100px;
  right: -190px;
  width: 200px;
  height: 200px;
  background: url("../img/content/icon-leaves.png");
  background-size: cover;
  background-repeat: no-repeat;
  transform: rotate(90deg); }
  @media (max-width: 1023px) {
    .form__body::before {
      right: 0;
      top: -120px;
      transform: rotate(180deg); } }

.form__body::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(51, 51, 51, 0.9) url("../img/loading.gif") center/50px no-repeat;
  opacity: 0;
  visibility: hidden;
  transition: all 0.5s ease 0s; }

.form__body._sending::after {
  opacity: 1;
  visibility: visible; }

.form__item {
  margin: 0px 0px 20px 0px; }

.form__label {
  font-size: 18px;
  display: block;
  margin: 0px 0px 10px 0px; }

.form__input {
  height: 50px;
  padding: 0px 20px;
  width: 100%;
  font-size: 18px;
  transition: all 0.5s ease 0s;
  border: none;
  border-bottom: 2px solid #255E0F; }

.form__input:focus {
  box-shadow: 0 0 15px #255E0F; }

.form__input._error {
  box-shadow: 0 0 15px red; }

textarea.form__input {
  min-height: 120px;
  resize: vertical;
  padding: 20px; }

.options {
  padding: 10px 0px 0px 0px; }

.options__item {
  margin: 0px 0px 10px 0px; }

.options__input {
  display: none; }

.options__input:checked + .options__label::after {
  transform: scale(1); }

.options__label {
  display: inline-flex;
  font-size: 16px;
  line-height: 140%;
  align-items: center;
  position: relative;
  cursor: pointer; }

.options__label::before {
  content: "";
  align-self: flex-start;
  flex: 0 0 24px;
  height: 24px;
  background-color: #fff;
  border-radius: 50%;
  margin: 0px 10px 0px 0px; }

.options__label::after {
  transition: transform 0.5s ease 0s;
  content: "";
  position: absolute;
  top: 4px;
  left: 4px;
  width: 16px;
  height: 16px;
  background-color: #C5AC2D;
  border-radius: 50%;
  transform: scale(0); }

.checkbox._error .checkbox__label::before {
  box-shadow: 0 0 15px red; }

.checkbox__input {
  display: none; }

.checkbox__input:checked + .checkbox__label::after {
  transform: scale(1); }

.checkbox__label {
  width: 100%;
  font-size: 16px;
  line-height: 140%;
  display: inline-flex;
  align-items: center;
  position: relative;
  cursor: pointer; }

.checkbox__label::before {
  content: "";
  align-self: flex-start;
  flex: 0 0 24px;
  height: 24px;
  background-color: #D3E1E2;
  border-radius: 4px;
  margin: 0px 10px 0px 0px;
  border: 1px solid #255E0F; }

.checkbox__label::after {
  transition: transform 0.5s ease 0s;
  content: "";
  width: 16px;
  height: 16px;
  position: absolute;
  top: 4px;
  left: 4px;
  background-color: #8CC62A;
  border-radius: 4px;
  transform: scale(0); }

.checkbox__label a {
